import React from 'react';
import { CopyBlock, dracula } from 'react-code-blocks';

const CSS_CODE = `/* Default style for all challenges containers. */
.challenge-container {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;

  /* Overridden for this component */
  width: 100%;
  justify-content: center;
}

.squares {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.squares .challenge-07-span {
  height: 2rem;
  width: 2rem;
  position: absolute;
  border-radius: 4px;
  top: -16px;
  left: -16px;
  animation: spin 2s linear infinite;
}

.squares .challenge-07-span:nth-child(1) {
  background-color: #ffffff;
  animation-delay: 0.7s;
}

.squares .challenge-07-span:nth-child(2) {
  background-color: #55acee;
  animation-delay: 1.4s;
}

.squares .challenge-07-span:nth-child(3) {
  background-color: #dd4b39;
}

@keyframes spin {
  0% {
    top: -16px;
    left: -16px;
  }

  12.5% {
    top: -16px;
    left: 50px;
  }

  25% {
    top: -16px;
    left: 50px;
  }

  37.5% {
    top: 50px;
    left: 50px;
  }

  50% {
    top: 50px;
    left: 50px;
  }

  62.5% {
    top: 50px;
    left: -16px;
  }

  75% {
    top: 50px;
    left: -16px;
  }

  82.5% {
    top: -16px;
    left: -16px;
  }

  100% {
    top: -16px;
    left: -16px;
  }
}
`;

const Code: React.FC = () => {
  return (
    <div className="container-code">
      <CopyBlock
        text={CSS_CODE}
        language="css"
        showLineNumbers={false}
        theme={dracula}
        onCopy={false}
      />
    </div>
  );
};

export default Code;
